<template>
<div>
  <h1>Find which sports are best for you based on your preferences.</h1>
  <div class="searchBox">
    <div class="button" >
      <div class="selected" v-if="this.$root.$data.parameters.hasBall === true">Ball</div>
      <div class="unselected" v-else @click="changeHasBall()">Ball</div>
    </div>
    <div class="button">
      <div class="selected" v-if="this.$root.$data.parameters.hasBall === false">No Ball</div>
      <div class="unselected" v-else @click="changeHasBall()">No Ball</div>
    </div>
  </div>

  <div class="searchBox">
    <div class="button" >
      <div class="selected" v-if="this.$root.$data.parameters.outdoor === true">Outdoor</div>
      <div class="unselected" v-else @click="changeOutdoor()">Outdoor</div>
    </div>
    <div class="button">
      <div class="selected" v-if="this.$root.$data.parameters.outdoor === false">Indoor</div>
      <div class="unselected" v-else @click="changeOutdoor()">Indoor</div>
    </div>
  </div>

  <div class="searchBox">
    <div class="button" >
      <div class="selected" v-if="this.$root.$data.parameters.difficulty === 1">Easy</div>
      <div class="unselected" v-else @click="changeDifficulty(1)">Easy</div>
    </div>
    <div class="button">
      <div class="selected" v-if="this.$root.$data.parameters.difficulty === 2">Medium</div>
      <div class="unselected" v-else @click="changeDifficulty(2)">Medium</div>
    </div>
    <div class="button">
      <div class="selected" v-if="this.$root.$data.parameters.difficulty === 3">Hard</div>
      <div class="unselected" v-else @click="changeDifficulty(3)">Hard</div>
    </div>
  </div>


  <div class="allSports">
    <div v-for="sport in sports" :key="sport.id">
      <div v-if="
       _self.$root.$data.parameters.hasBall === sport.hasBall &&
       _self.$root.$data.parameters.outdoor === sport.outdoor &&
       _self.$root.$data.parameters.difficulty === sport.difficulty
       ">
      <div class="sport">
<!-- This div will only have sports that equal all the parameters -->
        <div class="sport-title">
          <div class="image">
            <img :src="'/images/' + sport.img">
            <h3>{{sport.name}}</h3>
          </div>
        </div>
        <div class="sport-description">

          <div v-if="sport.hasBall === true">Has a ball.</div>
          <div v-else>Does not have a ball.</div>

          <div v-if="sport.isTeam === true">Team sport.</div>
          <div v-else>Individual Sport.</div>

          <div v-if="sport.outdoor === true">Performed outdoors.</div>
          <div v-else>Performed indoors.</div>

          <div v-if="sport.difficulty === 1">Difficulty: Easy</div>
          <div v-if="sport.difficulty === 2">Difficulty: Normal</div>
          <div v-if="sport.difficulty === 3">Difficulty: Hard</div>
        </div>
        <div class="" v-if="sport.saved === false">
          <button class="" @click='saveButton(sport)'>
            Add to Saved
          </button>
        </div>
        <div class="" v-else>
          <button class="" @click='removeButton(sport)'>
            Remove from Saved
          </button>
        </div>
      </div>
    </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: "Sports",
  computed: {
    sports() {
      return this.$root.$data.sports;
    },
    parameters() {
      return this.$root.$parameters;
    },
  },
  methods: {
    changeHasBall() {
      this.$root.$data.parameters.hasBall = !this.$root.$data.parameters.hasBall;
    },
    changeOutdoor() {
      this.$root.$data.parameters.outdoor = !this.$root.$data.parameters.outdoor;
    },
    changeDifficulty(num) {
      this.$root.$data.parameters.difficulty = num;
    },
    saveButton(sport) {
      sport.saved = true;
    },
    removeButton(sport) {
      sport.saved = false;
    }
  }
}

</script>

<style media="screen">

h1 {
  margin-left: 15%;
  margin-right: 15%;
}
.searchBox{
    margin-left: 15%;
    margin-right: 15%;
    display: flex;
    flex-flow: row;
    justify-content: space-around;
    margin-bottom: 5px;
  }

.button {
  /* background-color: gray; */
  width: 50%;
  border: black solid 1px;
}
.unselected:hover {
  cursor: pointer;
}
.selected {
  background-color: #bfbfbf;
}
.unselected {
  background-color: #e6e6e6;
}

</style>
