import Vue from 'vue'
import App from './App.vue'
import router from './router'
import sports from './data.js'

Vue.config.productionTip = false

let data = {
  sports: sports,
  saved: [],
  parameters: {
    hasBall: true,
    difficulty: 1,
    isTeam: true,
    outdoor: true,
  }
}

new Vue({
  router,
  data,
  render: h => h(App)
}).$mount('#app')
